import Agent from '../../../models/Agent';

const syncCurrentStatus = async (req, res) => {
	try{
		const agent = await Agent.findOne({ 
			'_id': req.user._id 
		}, 'account.username account.passcode account.role account.activate defaultSetting currentStatus players')
		.populate({ 
			path: 'players', 
			select: 'account.username'
		})
		res.json(agent)
	}catch(err){
		throw err
	}
}

export default syncCurrentStatus;